<template>
	<div class="wraper">
		<div class="lotterybg">
			<img :src="lotterybg">
		</div>
		<div class="lotteryContent" :style="{width:lotterywidth[0]}">
			<img v-bind:style="{ transform: 'rotate3d(0,0,1,'+angle+'deg)'}" id="turntable" :src="contentBg">
		</div>
		<div class="pointer" :style="{width:lotterywidth[1]}" @click="clickLottery">
			<img :src="pointerbg">
		</div>
	</div>
</template>

<script>
export default {
	data(){
		return {
			angle: this.lotteryAngle,//
			flag: this.lotteryFlag,//
			acceleration: 0.5,//0.5
			r_acceleration: 0.1,//0.1
			firstTimes: 48,//48
			prizeNum: this.lotteryPrizenum,
			lotterybg: this.lotteryBg,//
			contentbg: this.contentBg,//
			pointerbg: this.pointerBg,//
			lotterywidth: this.lotteryWidth
		}
	},
	props: {
		lotteryAngle: {
			type: Number,
			default: 0
		},
		lotteryFlag: {
			type: Boolean,
			default: true
		},
		lotteryPrizenum: {
			type: Number,
			required: true
		},
		lotteryPrizeno: {
			type: Number,
			required: true
		},
		lotteryBg: {
			type: String
		},
		contentBg: {
			type: String,
			required: true
		},
		pointerBg: {
			type: String,
			required: true
		},
		lotteryWidth: {
			type: Array,
			default: function(){
				return ["85%","35%"];
			}
		}
	},
	computed:{
		difference(){
			return (this.prizeNo / this.prizeNum) * 360 - 360 / this.prizeNum / 2;
		},
		prizeNo(){
			return this.lotteryPrizeno
		}
	},
	mounted(){
		
	},
	methods:{
		clickLottery(){
			if(!this.flag){ return false;};
			this.flag = false;
			this.startRotate();
		},
		startRotate(){
			var self = this;
			var difference = this.difference;
			var speed = 0,
				acceleration = self.acceleration,//
				r_acceleration = self.r_acceleration,//
				firstTimes = self.firstTimes,//48 highSpeed
				highSpeed = firstTimes * acceleration,//
				oneAngel = self.angle + firstTimes * (firstTimes - 1) / 2*acceleration + highSpeed,//
				secondTimes = 10,//highSpeed 10
				twoAngel = oneAngel + secondTimes * firstTimes * acceleration,//
				three = highSpeed / r_acceleration,//three highSpeed0
				add = 360 - (twoAngel + three*(three - 1) / 2 * r_acceleration) % 360 + difference,//
				twoAngelEnd = twoAngel + add,//
				threeAngel = twoAngelEnd + three * (three - 1) / 2 * r_acceleration;//
			function cicle(){
				if(self.angle < oneAngel) {
					speed += acceleration;
					self.angle += speed;
				}
				else if(self.angle >= oneAngel && self.angle < twoAngel){
					self.angle += speed;
				}
				else if(self.angle.toFixed(2) == twoAngel.toFixed(2)){
					self.angle += add; 
				}
				else if(self.angle > twoAngel && self.angle <= threeAngel){
					speed -= r_acceleration;
					if(speed <=0){
						speed = 0;
						self.angle = threeAngel%360;
						self.flag = true;
						self.$emit("lotteryDone",{
							prizeNo: self.prizeNo,
							prizeNum: self.prizeNum
						})
						return false;
					}
					self.angle += speed;
				}
				requestAnimationFrame(cicle);
			}
			cicle();
		}
	}
}
</script>

<style lang="scss" scoped>
	.wraper{
		position: relative;
		max-width: 500px;
		img{
			width: 100%;
		}
		.lotteryContent{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			-webkit-transform: translate(-50%,-50%);
		}
		.pointer{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			-webkit-transform: translate(-50%,-50%);
		}
	}
</style>





